.maintenance {
  width: 411px;
  height: 280px;
}
.maintenance .tiele {
  width: 137px;
  height: 37px;
  line-height: 37px;
  text-align: center;
  font-size: 16px;
  letter-spacing: 1px;
  color: #ffffff;
}
.maintenance .kuang {
  margin-top: 10px;
  width: 411px;
  height: 220px;
  overflow: hidden;
}
.maintenance .kuang .beijing {
  margin: 14px 0 0 13px;
  width: 392px;
  height: 196px;
  background-image: linear-gradient(90deg, rgba(2, 208, 255, 0.1) 0%, rgba(22, 32, 99, 0.1) 100%);
  overflow: hidden;
}
.maintenance .kuang .beijing .left {
  display: inline-block;
  float: left;
  width: 180px;
  height: 196px;
}
.maintenance .kuang .beijing .left ul {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}
.maintenance .kuang .beijing .left ul li {
  margin: 21px 0 0 8px;
  list-style: none;
  width: 100%;
  height: 35px;
}
.maintenance .kuang .beijing .left ul li span {
  display: inline-block;
  width: 118px;
  height: 35px;
  line-height: 35px;
  text-align: center;
  font-size: 14px;
  letter-spacing: 1px;
  color: #ffffff;
}
.maintenance .kuang .beijing .left ul li p {
  position: relative;
  top: 10px;
  display: inline-block;
  font-size: 12px;
  color: #ffffff;
}
.maintenance .kuang .beijing .left ul li p i {
  font-style: normal;
  font-size: 22px;
  font-weight: 700;
  color: #17e6ff;
}
.maintenance .kuang .beijing .right {
  display: inline-block;
  float: left;
  width: 210px;
  height: 196px;
}
.maintenance .kuang .beijing .right ul {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}
.maintenance .kuang .beijing .right ul li {
  margin: 21px 0 0 3px;
  list-style: none;
  width: 100%;
  height: 35px;
}
.maintenance .kuang .beijing .right ul li span {
  display: inline-block;
  width: 118px;
  height: 35px;
  line-height: 35px;
  text-align: center;
  font-size: 14px;
  letter-spacing: 1px;
  color: #ffffff;
}
.maintenance .kuang .beijing .right ul li p {
  position: relative;
  top: 10px;
  display: inline-block;
  font-size: 12px;
  color: #ffffff;
}
.maintenance .kuang .beijing .right ul li p i {
  font-style: normal;
  font-size: 22px;
  font-weight: 700;
  color: #17e6ff;
}
